import React from 'react'
import {Link, graphql} from 'gatsby'
import {css} from '@emotion/core'
import {rhythm} from '../utils/typography'
import Layout from '../components/layout'
import {Helmet} from 'react-helmet'
import {Flex, Box} from '@rebass/grid/'

export default ({data}) => {
  console.log(data)
  return (
    <Layout>
      <Flex p={5} flexWrap="wrap" mt={1} width={1}>
        <Helmet>
          <meta charSet="utf-8" />
          <title>{data.site.siteMetadata.title}</title>
          <link
            rel=""
            target="_blank"
            href="http://twitter.com/positivneutron"
          />
        </Helmet>
        <Box
          width={[1, 1 / 2]}
          css={css`
            text-align: center;
          `}
        >
          <img
            src={data.allSrcJson.edges[0].node.profilPicture}
            alt="positive-neutron"
            css={css`
              border-radius: 50%;
              height: auto;
              max-width: 50%;
              @media only screen and (max-width: 420px) {
                max-width: 100%;
              }
            `}
          />
        </Box>
        <Box width={[1, 1 / 2]} pt={4}>
          <p
            css={css`
              font-size: ${rhythm(0.8)};
              color: #707070;
              max-width: 500px;
            `}
          >
            {data.allSrcJson.edges[0].node.about.map((node, index) => (
              <span
                key={index}
                css={css`
                  width: 100px;
                `}
              >
                {node}
                <br />
                <br />
              </span>
            ))}
          </p>
        </Box>
      </Flex>
    </Layout>
  )
}

export const query = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allSrcJson {
      edges {
        node {
          profilPicture
          about
        }
      }
    }
  }
`
